html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: hidden;
}

#name_display {
  text-transform: capitalize;
}

.schedule {
  flex-grow: 1;
  overflow: hidden;
}

.help {
  cursor: help;
}

.vis-inner {
  width: 100%;
}

.vis-dot,
.vis-line {
  display: none;
}

.vtuber {
  text-align: center;
}

.vtuber img {
  width: 5em;
  border-radius: 2.5em;
}

.stream img {
  height: 6em;
}

.vis-item:hover {
  z-index: 10000 !important;
}
